import { pluginName } from './constants';

const { CKEDITOR } = window;

CKEDITOR.plugins.add(pluginName, {
  requires: 'richcombo',
  init(editor) {
    const colorMap = CKEDITOR.tools.get_plugin_config(pluginName, editor);

    /* ------------------------------------------------------------------ */
    /* Utilities                                                          */
    /* ------------------------------------------------------------------ */
    const utils = {
      cloneElement(source) {
        const clone = new CKEDITOR.dom.element(source.getName());
        Object.entries(source.getAttributes()).forEach(([k, v]) => clone.setAttribute(k, v));
        return clone;
      },

      /** Wrap `node` with cloned ancestors (innermost first). */
      wrapWithAncestors(node, chain) {
        return chain.reduceRight((inner, outer) => {
          const wrapper = utils.cloneElement(outer);
          wrapper.append(inner);
          return wrapper;
        }, node);
      },

      /** Split a text node into [before, selected, after] according to `range`. */
      splitTextByRange(textNode, range) {
        const start = textNode.equals(range.startContainer) ? range.startOffset : 0;
        const end = textNode.equals(range.endContainer) ? range.endOffset : textNode.getLength();
        return [textNode.substring(0, start), textNode.substring(start, end), textNode.substring(end)];
      }
    };

    /* ------------------------------------------------------------------ */
    /* Ensure colour <span> wraps the inline tag, not vice‑versa           */
    /* ------------------------------------------------------------------ */
    function liftColorSpans(range) {
      const walkRange = range.clone();
      walkRange.enlarge(CKEDITOR.ENLARGE_INLINE);
      const walker = new CKEDITOR.dom.walker(walkRange);
      walker.evaluator = n => n?.type === CKEDITOR.NODE_ELEMENT && n.getName() === 'span' && n.getStyle('color');

      let span;
      while ((span = walker.next())) {
        const parentInline = span.getParent();
        if (!parentInline || parentInline.getName() === 'span') continue;
        const firstChild = span.getFirst();
        if (firstChild?.type === CKEDITOR.NODE_ELEMENT && firstChild.getName() === parentInline.getName()) continue;

        const inlineClone = utils.cloneElement(parentInline);
        while (span.getFirst()) inlineClone.append(span.getFirst().remove());
        span.append(inlineClone);
      }
    }

    /* ------------------------------------------------------------------ */
    /* Remove only the colour while keeping other inline styles            */
    /* ------------------------------------------------------------------ */
        /* ------------------------------------------------------------------ */
    /* Remove only colour spans – collect nodes first, then mutate DOM    */
    /* ------------------------------------------------------------------ */
    function removeColor(range) {
      // Walk an enlarged clone so we capture partially‑selected spans
      const enlarged = range.clone();
      enlarged.enlarge(CKEDITOR.ENLARGE_INLINE);
      const walker = new CKEDITOR.dom.walker(enlarged);
      walker.evaluator = n =>
        n.type === CKEDITOR.NODE_TEXT && n.getAscendant('span', true)?.getStyle('color');

      // Collect targets first to avoid walker skipping nodes after DOM changes
      const targets = [];
      let node;
      while ((node = walker.next())) targets.push(node);

      for (const textNode of targets) {
        const colorSpan = textNode.getAscendant('span', true);
        if (!colorSpan) continue;

        // Use ORIGINAL range for offsets so left/right fragments are detected
        const [before, middle, after] = utils.splitTextByRange(textNode, range);

        // Build chain of ancestors between textNode and colour span
        const chain = [];
        let ancestor = textNode.getParent();
        while (ancestor && !ancestor.equals(colorSpan)) {
          chain.unshift(ancestor);
          ancestor = ancestor.getParent();
        }

        const buildFragment = (content, keepColor) => {
          if (!content) return null;
          const wrapped = utils.wrapWithAncestors(new CKEDITOR.dom.text(content), chain);
          if (!keepColor) return wrapped;
          const spanCopy = utils.cloneElement(colorSpan);
          spanCopy.append(wrapped);
          return spanCopy;
        };

        [buildFragment(before, true), utils.wrapWithAncestors(new CKEDITOR.dom.text(middle), chain), buildFragment(after, true)]
          .filter(Boolean)
          .forEach(fragment => colorSpan.insertBeforeMe(fragment));

        textNode.remove();
        if (!colorSpan.getChildCount()) colorSpan.remove();
      }
    }

    /* ------------------------------------------------------------------ */
    /* RichCombo UI                                                       */
    /* ------------------------------------------------------------------ */
    editor.ui.addRichCombo('ApplyColor', {
      label: 'Apply Color',
      toolbar: 'styles',
      panel: {
        css: [CKEDITOR.skin.getPath('editor'), ...editor.config.contentsCss],
        multiSelect: false
      },
      init() {
        this.add('default', 'Remove Color', 'Remove Color');
        for (const [label, value] of Object.entries(colorMap)) {
          this.add(value, label, label);
        }
      },
      onRender() {
        this.setValue('');
      },
      onClick(choice) {
        editor.focus();
        let selection = editor.getSelection();
        if (!selection) return;
        editor.fire('lockSnapshot');

        if (choice === 'default') {
          // Ensure spans are well‑formed first
          selection.getRanges().forEach(liftColorSpans);
          selection = editor.getSelection();
          selection?.getRanges().forEach(removeColor);
        } else {
          const colorStyle = new CKEDITOR.style({ element: 'span', styles: { color: choice } });
          editor.applyStyle(colorStyle);
          selection = editor.getSelection();
          selection?.getRanges().forEach(liftColorSpans);
        }

        editor.fire('unlockSnapshot');
      }
    });
  }
});
